/* Navigation bar styles */
.navigation-bar {
	position: fixed;
	bottom: 0;
	width: 100%;
	background-color: #000000;
	padding: 1em 0;
	display: flex;
	justify-content: space-evenly;
}

.navigation-bar a {
	color: white;
	text-decoration: none;
	padding: 0.5em;
	font-weight: bold;
}

/* Section styles */
.hero {
	align-items: center;
	display: flex;
	height: 100vh;
	position: relative;
	background-image: url('../images/home-bg.png');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
}

.course-details {
	padding: 80px 0;
	background-color: #000319;
	color: #f6f6f6;
}

.instructor {
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	background-image: url('https://ann.axiomthemes.com/splash/src/img/reward/bg.jpg');
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	background-attachment: fixed;
	color: #f6f6f6;
}

/* Hero styles */
.hero-text {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.hero-text h1 {
	font-size: 4em;
	color: #f6f6f6;
	background: rgba(0, 0, 0, 0.7);
	padding: 0.5em;
	border-radius: 10px;
	margin-bottom: 0.5em;
	animation: pulse 2s infinite ease-in-out;
	width: 750px;
}

.hero-text p {
	color: #ddd;
	background: rgba(0, 0, 0, 0.7);
	padding: 0.5em;
	border-radius: 10px;
	max-width: 600px;
	margin-bottom: 1em;
}

.continue-button {
	padding: 0.5em 1em;
	background-image: linear-gradient(to left, #025d48, #258a59, #5ab75b);
	border: none;
	cursor: pointer;
	color: white;
	border-radius: 20px;
	font-weight: bold;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
	position: absolute;
	bottom: 100px;
	left: 50%;
	transform: translateX(-50%);
}


/* IMAGENES */

.images-container {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin-bottom: 50px;
}

.details-block{
	text-align: center;
}

.image-wrapper {
	position: relative;
	border-radius: 8px;
	overflow: hidden;
	margin: 5px;
	width: 100%;
}

.image-wrapper img {
	width: 320px;
	height: auto;
	border-radius: 8px;
	transition: transform 0.3s ease;
}

.image-hover:hover img {
	transform: scale(1.05);
}

.image-hover:hover .hover-text {
	bottom: 0;
}

.hover-text {
	position: absolute;
	bottom: -100%;
	left: 0;
	right: 0;
	background-color: rgba(0, 0, 0, 0.7);
	color: white;
	padding: 20px;
	transition: bottom 0.3s ease;
	max-height: 100%;
	overflow: auto;
}

.image-description {
	font-weight: bold;
}

/* INSTRUCTOR */

.instructor img{
	width: 250px;
	border-radius: 100%;
}

.instructor h2{
	margin: 0;
}
/* Responsive design adjustments */
@media (max-width: 992px) {
	.hero-text h1 {
		font-size: 48px;
		width: 600px;
	}
}

@media (max-width: 768px) {
	.hero-text h1 {
		font-size: 36px;
		width: 500px;
	}

	.hero-text p {
		max-width: 500px;
		font-size: 14px;
	}
}

@media (max-width: 576px) {
	.hero-text h1 {
		font-size: 32px;
		width: 400px;
	}

	.hero-text p {
		max-width: 400px;
		font-size: 12px;
	}
}

@media (max-width: 480px) {

	.hero-text h1 {
		font-size: 24px;
		width: 320px;
	}

	.hero-text p {
		display: none;
	}

	.image-hover:hover img {
		transform: none;
		/* Elimina la transformación en dispositivos móviles para evitar problemas de usabilidad */
	}
}
